.header {
  padding: 10px 0px 10px 0px;
  width: 90vw;
  margin: auto;
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.header-outer {
  border: 1px solid rgba(205, 208, 208, 0.778);
}
.header-become {
  padding: 10px;
  border-radius: 25px;
  font-weight: 600;
}
.header-become:hover {
  background-color: rgba(218, 225, 225, 0.836);
  cursor: pointer;
}
.header-logo {
  height: 30px;
}
.header-profile {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.header-pro {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border: 1px solid rgb(180, 174, 174);
  border-radius: 25px;
  padding: 3px;
}
.header-pro:hover {
  box-shadow: 2px 3px 2px 0px rgb(184, 181, 181);
  cursor: pointer;
}/*# sourceMappingURL=index.css.map */